// 1.引入
import Search from '@/components/search'
const PAGE_SIZE = 6
export default {
  // 2.注册组件
  components: {
    // 组件名:组件
    // Search: Search
    Search
  },
  data () {
    return {
      navArr: [
        '综合',
        '销量',
        '价格'
      ],
      activeIndex: 0,
      // 搜索关键字
      keyword: '',
      // 搜索结果列表
      goodsList: [],
      isLastPage: false,
      isFixed: false
    }
  },
  onLoad (options) {
    // console.log(options.keyword)
    this.keyword = options.keyword
    this.isRequest = false
    this.reload()
  },
  onPullDownRefresh () {
    this.isFixed = false
    this.reload()
  },
  // 上拉加载下一页
  onReachBottom () {
    this.pageNum++
    this.search()
  },
  onPageScroll () {
    this.isFixed = true
  },
  methods: {
    // 跳转商品详情
    toItem (goodsId) {
      wx.navigateTo({ url: '/pages/item/main?goodsId=' + goodsId })
    },
    inputHandler (data) {
      this.keyword = data
      this.reload()
    },

    reload () {
      this.isLastPage = false
      // ==============================
      this.goodsList = [] // 清空
      // ==============================
      this.pageNum = 1
      this.search()
    },
    // 根据关键字查询商品
    search () {
      // 如果请求中或者是最后一页，就不发请求
      if (this.isRequest || this.isLastPage) {
        return
      }
      // 发请求前，isRequest置为true
      this.isRequest = true
      this.$request({
        url: '/api/public/v1/goods/search',
        // noLoading: true,
        data: {
          query: this.keyword,
          pagesize: PAGE_SIZE,
          pagenum: this.pageNum
        }
      }).then(data => {
        // console.log(data)
        this.goodsList = [...this.goodsList, ...data.goods]
        // 这里判断是不是最后一页
        if (this.goodsList.length === data.total) {
          this.isLastPage = true
        }
      }).finally(() => {
        // 请求完，isRequest为false
        this.isRequest = false
      })
    }
  }

}
